<template>
  <div class="tags">
    <div
        class="tags__tag"
        v-for="(tag, index) in tags"
        :key="index"
        :style="{ color : tag.color }"
    >
      {{ tag.name }}
    </div>
  </div>
</template>

<script>
import { Vue, Component, Prop } from 'vue-property-decorator';
import { namespace } from 'vuex-class';

const calendar = namespace('main-module/tag');

@Component
class Tags extends Vue {
  @Prop()
  tagsIds;

  @Prop({
    type: Boolean,
    default: false,
  })
  small;

  @calendar.Getter('tagsPack')
  tagsPack;

  get tags() {
    return this.tagsPack(this.tagsIds);
  }
}

export default Tags;
</script>

<style lang="scss">

.tags {
  display: flex;
  position: relative;
  font-weight: bolder;

  .tags__tag {
    display: flex;
    align-items: center;
    font-size: 14px;

    &:not(:last-child) {
      margin-right: 6px;
    }
  }

  &.tags--small {
    .tags__tag {
      white-space: nowrap;

      &:not(:last-child) {
        margin-right: 3px;
      }
    }
  }
}

</style>
