<template>
  <div class="event" @click="onEventClick">
    <div class="event__name">{{ eventData.name }}</div>
    <div class="event__additional-data">
      <div class="event__age">{{ eventData.age }}</div>
      <tags
          class="event__tags"
          :tags-ids="eventData.tags"
      ></tags></div>
  </div>
</template>

<script>
import { Vue, Component, Prop } from 'vue-property-decorator';
import Tags from '../Tags.vue';

@Component({
  components: {
    Tags,
  },
})
class Event extends Vue {
  @Prop()
  eventData;

  onEventClick() {
    this.$emit('event-click', this.eventData);
  }
}

export default Event;
</script>

