<template>
  <h-dialog
      :value="modalIsOpened"
      max-width="620"
      hide-title
      @close="closeModal"
      :persistent="!currentCity"
      :hide-close-button="!currentCity"
  >
    <template v-slot:title>
      <h3 class="city__info-text">{{ $t('city.infoText') }}</h3>
    </template>
    <div class="city">
      <div
          class="city__item"
          v-for="(city, cityId) in cities"
          :key="cityId"
      >
        <span
            class="city__item-name"
            @click="selectCity(cityId)"
        >
          {{ city.name }}
        </span>
      </div>
    </div>
  </h-dialog>
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';
import { namespace } from 'vuex-class';

const city = namespace('main-module/city');

@Component
class CityModal extends Vue {
  @city.Getter('cities')
  cities;

  @city.Getter('modalIsOpened')
  modalIsOpened;

  @city.Mutation('closeModal')
  closeModal;

  @city.Action('setCurrentCity')
  setCurrentCity;

  @city.Mutation('setCityIsReady')
  setCityIsReady;

  @city.Getter('currentCity')
  currentCity;

  selectCity(cityId) {
    this.setCurrentCity(cityId);
    this.setCityIsReady();
    this.closeModal();
  }
}

export default CityModal;
</script>

<style lang="scss">
.city {
  transition: 150ms;

  .city__item {
    color: $link-text-color;
    font-size: 18px;

    .city__item-name {
      cursor: pointer;

      &:hover {
        color: $link-text-color--hover;
      }

      &:active {
        color: $link-text-color--active;
      }
    }

    &:not(:first-child) {
      margin-top: 5px;
    }
  }
}
</style>
