<template>
  <div class="main-header">
    <div class="main-header__wrapper container">
      <div class="main-header__right-content">
        <div
            v-if="haveUserData"
            class="main-header__user-data"
        >
          <span class="main-header__user-name">{{ userData.username }}</span>
          <h-btn
              @click="goToEditCalendar"
              small
              transparent
              color="#ffffff"
          >
            <span class="mdi mdi-calendar-edit"></span>
          </h-btn>
          <logout color="#ffffff"></logout>
        </div>
        <h-btn
            v-else
            @click="enter"
            transparent
            color="#ffffff"
        >
          {{ $t('header.enter') }}
        </h-btn>
      </div>
    </div>
    <div class="container">
      <calendar-page-picker class="mt-3"></calendar-page-picker>
    </div>
  </div>
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import CalendarPagePicker from './CalendarPagePicker.vue';
import Logout from '../Logout.vue';

const user = namespace('user');

@Component({
  components: {
    CalendarPagePicker,
    Logout,
  },
})
class MainHeader extends Vue {
  @user.Getter('haveUserData')
  haveUserData;

  @user.Getter('userData')
  userData;

  enter() {
    this.$router.push({ name: 'Auth' });
  }

  goToEditCalendar() {
    this.$router.push({ name: 'Manage' });
  }
}

export default MainHeader;
</script>

