<template>
  <div class="calendar-cell" :class="classes">
    <h-btn
        v-if="haveEvents"
        class="calendar-cell__title"
        @click="onCellClick"
        transparent
        small
    >
      <span class="calendar-cell__date">{{ titleText }}</span>
      <span class="mdi mdi-arrow-left mdi-rotate-135"></span>
    </h-btn>
    <div v-else class="calendar-cell__title">
      <span class="calendar-cell__date">{{ titleText }}</span>
    </div>
    <div class="calendar-cell__events-wrapper">
      <template v-if="haveEvents">
        <event
            v-for="(event, index) in events"
            :key="index"
            :event-data="event"
            @event-click="onEventClick"
        ></event>
      </template>
      <template v-else>
        <div class="calendar-cell__no-data">
          {{ $t('calendar.calendarCell.noData') }}
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { Vue, Component, Prop } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import Event from './Event.vue';

const resolution = namespace('resolution');
const calendar = namespace('main-module/calendar');

@Component({
  components: {
    Event,
  },
})
class CalendarCell extends Vue {
  @Prop()
  cellData;

  @resolution.Getter('smallSize')
  smallSize;

  @calendar.Getter('weekDays')
  weekDays;

  get events() {
    return this.cellData.eventsData || [];
  }

  get haveEvents() {
    return !!this.events.length;
  }

  get titleText() {
    if (!this.smallSize) {
      return `${this.cellData.date?.format('ll')}, ${this.$t('calendar.calendarCell.eventsCount')}: ${this.events.length}`;
    } else {
      return `${this.cellData.date?.format('ll')}, ${this.weekDays[this.cellData.weekDay]}, ${this.$t('calendar.calendarCell.eventsCount')}: ${this.events.length}`;
    }
  }

  onEventClick($event) {
    this.$emit('event-click', $event);
  }

  onCellClick() {
    this.$emit('cell-click', this.cellData);
  }

  get classes() {
    return {
      'calendar-cell--empty': !this.haveEvents,
    };
  }
}

export default CalendarCell;
</script>

<style lang="scss">
.calendar-cell {
  width: calc(100% / 7);
  display: flex;
  flex-direction: column;
  border: solid 1px #f5f5f5;
  box-sizing: border-box;

  .calendar-cell__title {
    padding: 8px 6px!important;
    flex: 0 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 0px;
    text-transform: lowercase;

    span {
      font-size: 12px;
      color: #999;
    }

    .h-btn__content {
      justify-content: space-between;
      span.calendar-cell__date {
        text-align: left;
      }
    }
  }

  .calendar-cell__events-wrapper {
    padding: 0px 8px 8px;
    overflow: auto;
    flex: 1 0;

    .calendar-cell__no-data {
      font-size: 14px;
      color: #d69fb9;
    }

    .event:not(:first-child) {
      margin-top: 8px;
    }
  }
}
</style>
