<template>
  <div class="logout">
    <slot>
      <h-btn
          @click="logout"
          small
          transparent
          :color="color"
      >
        <span class="mdi mdi-logout"></span>
      </h-btn>
    </slot>
  </div>
</template>

<script>
import { Vue, Component, Prop } from 'vue-property-decorator';
import { namespace } from 'vuex-class';

const user = namespace('user');

@Component
class Logout extends Vue {
  @Prop({
    type: String,
    default: '#02006b7a',
  })
  color;

  @user.Action('logout')
  logout;
}

export default Logout;
</script>
