<template>
  <div class="detailed-events-data">
    <span class="detailed-events-data__date">{{ date }}</span>
    <div class="detailed-events-data__wrapper-items">
      <detailed-event-mini
          v-for="(event, index) in dayData.eventsData"
          :key="index"
          :event-data="event"
      ></detailed-event-mini>
    </div>
  </div>
</template>

<script>
import { Vue, Component, Prop } from 'vue-property-decorator';
import DetailedEventMini from './DetailedEventMini.vue';

@Component({
  components: {
    DetailedEventMini,
  },
})
class DetailedEventsData extends Vue {
  @Prop()
  dayData;

  get date() {
    return this.dayData.date.format('dddd[,] ll');
  }
}

export default DetailedEventsData;
</script>

