<template>
  <div class="calendar-page-picker">
    <div class="calendar-page-picker__city">
      <span class="calendar-page-picker__value" @click="openCityModal">{{ currentCity.name }}</span>
    </div>
    <h-btn
        transparent
        small
        @click="changeMonth('down')"
    >
      <span class="mdi mdi-arrow-left calendar-page-picker__change-button"></span>
    </h-btn>
    <div class="calendar-page-picker__data">
      <span class="calendar-page-picker__text">{{ $t('calendar.year') }}</span>
      <span class="calendar-page-picker__value">{{ visibleDate.year }}</span>
      <span class="calendar-page-picker__text">{{ $t('calendar.month') }}</span>
      <span class="calendar-page-picker__value">{{ visibleDate.month }}</span>
    </div>
    <h-btn
        transparent
        small
        @click="changeMonth('up')"
    >
      <span class="mdi mdi-arrow-right calendar-page-picker__change-button"></span>
    </h-btn>
    <h-load-mask v-show="loadMaskIsVisible"></h-load-mask>
  </div>
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';
import { namespace } from 'vuex-class';

const calendar = namespace('main-module/calendar');
const city = namespace('main-module/city');

@Component
class CalendarPagePicker extends Vue {
  @calendar.Getter('visibleDate')
  visibleDate;

  @calendar.Action('setCurrentPage')
  setCurrentPage;

  @city.Getter('currentCity')
  currentCity;

  @city.Mutation('openModal')
  openCityModal;

  @calendar.Action('changeMonth')
  changeMonth;

  @calendar.Getter('haveCalendarPage')
  haveCalendarPage;

  @calendar.Getter('calendar')
  calendar;

  get loadMaskIsVisible() {
    return this.calendar.some(cItem => !cItem.status);
  }

  created() {
    if (!this.haveCalendarPage) {
      this.setCurrentPage();
    }
  }
}

export default CalendarPagePicker;
</script>

<style lang="scss">

.calendar-page-picker {
  display: flex;
  align-items: center;
  padding: 6px;

  .calendar-page-picker__city {
    margin-right: 5px;
  }

  .calendar-page-picker__text {
    color: #7a7a7a;
    font-size: 16px;
    margin-right: 3px;
  }

  .calendar-page-picker__value {
    color: $link-text-color;
    font-size: 20px;
    cursor: pointer;

    &:hover {
      color: $link-text-color--hover;
    }

    &:active {
      color: $link-text-color--active;
    }

    @media (max-width: 599px) {
      font-size: 18px;
    }
  }

  .calendar-page-picker__data {
    margin: 0px 5px;

    > span:nth-child(2) {
      margin-right: 6px;
    }
  }
}
</style>
