<template>
  <div class="calendar">
    <div class="calendar__wrapper container">
      <div class="calendar__weekdays mt-4" v-if="!smallSize">
        <div
            class="calendar__weekday"
            v-for="(weekDay, index) in weekDays"
            :key="index"
        >{{ weekDay }}
        </div>
      </div>
      <transition-group
          tag="div"
          :name="animationName"
          class="transition-wrapper calendar__pages-wrapper h-card"
      >
        <calendar-page
            class="calendar__page"
            v-for="page in calendar"
            :key="`${page.year}_${page.month}`"
            :page-data="page"
            v-show="pageIsVisible(page)"
        ></calendar-page>
      </transition-group>
    </div>
  </div>
</template>

<script>
import { Vue, Component, Watch } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import CalendarPage from './CalendarPage.vue';

const calendar = namespace('main-module/calendar');
const resolution = namespace('resolution');

@Component({
  components: {
    CalendarPage,
  },
})
class Calendar extends Vue {
  @calendar.Getter('calendar')
  calendar;

  @calendar.Getter('currentPage')
  currentPage;

  @calendar.Getter('weekDays')
  weekDays;

  @resolution.Getter('smallSize')
  smallSize;

  animationName = '';

  @Watch('currentPage')
  onChangeCurrentPage(val, oldVal) {
    if ([0, 1].includes(this.calendar.length)) {
      this.animationName = '';
    } else {
      this.animationName = val.isBefore(oldVal) ? 'entity-slide-right' : 'entity-slide-left';
    }
  }

  pageIsVisible(pageDate) {
    return pageDate.year === this.currentPage.year() && pageDate.month === this.currentPage.month();
  }
}

export default Calendar;
</script>

<style lang="scss" scoped>
.calendar {
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;

  .calendar__wrapper {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-bottom: 12px;

    .calendar__weekdays {
      display: flex;
      width: 100%;
      flex: 0 1;
      color: #7a7a7a;

      .calendar__weekday {
        width: calc(100% / 7);
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }

    .calendar__pages-wrapper {
      display: flex;
      flex: 1 0;
      width: 100%;
      position: relative;
      overflow: hidden;
      padding: 0px;
      margin-top: 10px;

      .calendar__page {
        min-width: 100%;
        height: 100%;
      }
    }
  }
}

.entity-slide-right-enter-active,
.entity-slide-right-leave-active,
.entity-slide-left-enter-active,
.entity-slide-left-leave-active {
  @media (min-width: 960px) {
    will-change: transform;
    transition: all 500ms;
    position: absolute!important;
    top: 0px;
    bottom: 0px;
  }
}

.entity-slide-right-enter {
  transform: translate(-100%, 0);
  min-width: 100%;
}

.entity-slide-right-leave-active {
  transform: translate(100%, 0);
  min-width: 100%;
}

.entity-slide-left-enter {
  transform: translate(100%, 0);
  min-width: 100%;
}

.entity-slide-left-leave-active {
  transform: translate(-100%, 0);
  min-width: 100%;
}

</style>
