<template>
  <div class="main">
    <template v-if="citiesIsReady">
      <template v-if="currentCity">
        <main-header></main-header>
        <calendar></calendar>
      </template>
      <city-modal persistent></city-modal>
    </template>
  </div>
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import Calendar from '../components/main/Calendar.vue';
import MainHeader from '../components/main/MainHeader.vue';
import CityModal from '../components/main/CityModal.vue';

const tags = namespace('main-module/tag');
const city = namespace('main-module/city');

@Component({
  components: {
    Calendar,
    MainHeader,
    CityModal,
  },
})
class Main extends Vue {
  @tags.Action('loadTags')
  loadTags;

  @city.Getter('currentCity')
  currentCity;

  @city.Action('loadCities')
  loadCities;

  @city.Getter('citiesIsReady')
  citiesIsReady;

  async created() {
    this.loadTags();
    if (!this.citiesIsReady) this.loadCities();
  }
}

export default Main;
</script>

<style lang="scss" scoped>

.main {
  height: 100%;
  display: flex;
  flex-direction: column;

  @media (max-width: 959px) {
    height: auto;
  }

  .main-header {
    flex: 0 1;
    z-index: 1;
    position: sticky;
    top: 0;
  }

  .calendar {
    flex: 1 0;
    overflow: hidden;
    width: 100%;
  }
}
</style>
