<template>
  <div class="calendar-page" ref="calendar-page" :class="classes">
    <div class="calendar__cells-wrapper" v-if="haveDaysWithEvents">
      <div
          v-for="(emptyCell) in beforeEmptyCells"
          :key="emptyCell"
          class="calendar-cell calendar-cell--mock"
      ></div>
      <calendar-cell
          v-for="(calendarCell, index) in currentCalendarPageData"
          :key="index"
          :cell-data="calendarCell"
          @cell-click="openDetailedData('cell', $event)"
      ></calendar-cell>
      <div
          v-for="(emptyCell) in afterEmptyCells"
          :key="emptyCell"
          class="calendar-cell calendar-cell--mock"
      ></div>
    </div>
    <div class="calendar__no-events-in-month" v-else-if="pageData.status === 1">{{ $t('calendar.noEventsInMonth') }}</div>
    <h-dialog
        :value="detailedModalData.modal.value"
        max-width="720"
        @close="closeDetailedModal"
        :title="$t(detailedModalData.modal.title)"
    >
      <detailed-events-data
          v-if="detailedModalData.type === 'cell'"
          :day-data="detailedModalData.data"
      ></detailed-events-data>
    </h-dialog>
  </div>
</template>

<script>
import { Vue, Prop, Component } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import moment from 'moment';
import CalendarCell from './CalendarCell.vue';
import DetailedEventsData from './DetailedEventsData.vue';

const calendar = namespace('main-module/calendar');
const resolution = namespace('resolution');

@Component({
  components: {
    CalendarCell,
    DetailedEventsData,
  },
})
class CalendarPage extends Vue {
  @Prop({
    type: Object,
    default: () => ({
      year: 0,
      month: 0,
      date: moment(),
    }),
  })
  pageData;

  @calendar.Action('initCalendarPage')
  initCalendarPage;

  @calendar.Getter('calendarPageData')
  calendarPageData;

  @resolution.Getter('smallSize')
  smallSize;

  get beforeEmptyCells() {
    if (!this.currentCalendarPageData.length) return [];
    let emptyCellIndex = 0;
    const emptyCells = [];
    const emptyDaysCount = this.currentCalendarPageData[0].weekDay;
    for (let i = 0; i < emptyDaysCount; i += 1) {
      emptyCells.push(`emptyCells${emptyCellIndex}`);
      emptyCellIndex += 1;
    }
    return emptyCells;
  }

  get afterEmptyCells() {
    if (!this.currentCalendarPageData.length) return [];
    let emptyCellIndex = 15;
    const emptyCells = [];
    for (let i = 0; i < this.weeksCount * 7 - this.beforeEmptyCells.length - this.currentCalendarPageData.length; i += 1) {
      emptyCells.push(`emptyCells${emptyCellIndex}`);
      emptyCellIndex += 1;
    }
    return emptyCells;
  }

  get currentCalendarPageData() {
    const calendarPageData = this.calendarPageData({
      year: this.pageData.year,
      month: this.pageData.month,
    });
    return (this.smallSize ? calendarPageData.filter(cpItem => cpItem.eventsData.length) : calendarPageData) || [];
  }

  get haveDaysWithEvents() {
    return !!this.currentCalendarPageData.length;
  }

  get weeksCount() {
    return Math.ceil((this.beforeEmptyCells.length + this.currentCalendarPageData.length) / 7);
  }

  closeDetailedModal() {
    this.detailedModalData.modal.value = false;
    setTimeout(() => {
      this.detailedModalData.type = undefined;
      this.detailedModalData.data = undefined;
    }, 200);
  }

  detailedModalData = {
    type: undefined,
    data: undefined,
    modal: {
      value: false,
      title: '',
    },
  };

  openDetailedData(type, data) {
    this.detailedModalData.type = type;
    this.detailedModalData.data = data;
    this.detailedModalData.modal.value = true;
    this.detailedModalData.modal.title = `calendar.modal.${type}`;
  }

  get classes() {
    return {
      'calendar-page--small': this.smallSize,
    };
  }

  mounted() {
    this.$refs['calendar-page'].style.setProperty('--weeks-count', this.weeksCount);
  }
}

export default CalendarPage;
</script>

<style lang="scss">
.calendar-page {
  display: flex;
  flex-direction: column;
  --weeks-count: undefined;

  .calendar__cells-wrapper {
    height: 100%;
    display: flex;
    flex-wrap: wrap;

    .calendar-cell {
      height: calc(100% / var(--weeks-count) + 1px);

      &.calendar-cell--mock {
        background-color: #f7f7f7;
      }
    }
  }

  .calendar__no-events-in-month {
    padding: 12px;
    color: #d69fb9;
  }

  @media (max-width: 959px) {
    &.calendar-page {

      .calendar-page__weekdays {
        display: none;
      }
      .calendar__cells-wrapper {
        flex-direction: column;
        flex-wrap: nowrap;
        max-height: 100%;
        overflow: auto;

        .calendar-cell {
          width: 100%;
          height: 200px;
        }

        .calendar-cell.calendar-cell--mock {
          display: none;
        }
      }
    }
  }
}
</style>
